<template>
    <div class="c_right_box">
        <div class="c_right_top">
            <router-link to="index" class="on"></router-link>
            <a href="javascript:;" @click="handleDelete()"></a>
        </div>
        <el-table
            ref="multipleTable"
            :data="list"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" ></el-table-column>
            <el-table-column label="" width="80">
                <template slot-scope="scope">
                    <el-input v-model="scope.row.rank" @blur="handleRank(scope.row.id,scope.row.rank)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="" prop="ht_name"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope" >
                    <img v-for="v in scope.row.image" :key="v.id" :src="v" alt="" height="80">
                </template>
            </el-table-column>
            <el-table-column label="" prop="content"></el-table-column>
            <el-table-column label="" prop="pls"></el-table-column>
            <el-table-column label="" prop="dzs"></el-table-column>
            <el-table-column label="" prop="scs"></el-table-column>
            <el-table-column label="" width="80">
                <template slot-scope="scope">
                    <el-switch
                    v-model="scope.row.status"
                    active-value=1
                    inactive-value=2
                    active-color="#13ce66"
                    inactive-color="#999999"
                    @change="handleSwitch(scope.row.id,scope.row.status,'status')"
                    >
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column label="" prop="ctime" width="150"></el-table-column>
            <el-table-column label="" width="200">
                 <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="plHref(scope.row.id)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="10"
        layout="total,  prev, pager, next, jumper"
        :total="total">
        </el-pagination>

    </div>
</template>

<script type="text/javascript">
import {ajax,delAjax,rankAjax,switchAjax} from 'js/common'
export default {
    name: 'tz_list',
    data () {
        return {
            page:1,
            total:1,
            list:[],
            selectArr:[]
        }
	},
    created () {
        this.appData();
	},
    methods : {
        // 
		appData () {
            ajax('forum/tz_list',{page:this.page} , rs => {
                this.list = rs.data.list;
                this.total = Number(rs.data.nums);
            });
        },
        // 
        handleCurrentChange(val) {
            this.page = val;
            this.appData();
        },
        // /
        handleDelete (id = 0) {
            delAjax(this , 'forum_tz' , id);
        },
        // id
        handleSelectionChange(val) {
            this.selectArr = val;
        },
        // 
        handleRank (id,rank) {
            rankAjax('forum_tz' , id , rank)
        },
        // 
        handleSwitch (id , val , field) {
            switchAjax('forum_tz' , id , val , field)
        },
        plHref (id){
            this.$router.push({
                path: 'comment_list',
                query:{id:id}
            })
        }
    }
}
</script>    



