<template>
  <div id="wrap_a">
    <!--<el-header style="margin-bottom: 12px">-->
    <!--<header-nav class="header-nav"></header-nav>-->
    <!--</el-header>-->
    <div class="product-main" ref="myMain">
      <div class="pro-filter">
        <div style="margin-left:30px" v-if="!isShowAdvancedQuery">
          <el-select v-model="proParams.ProSelect" placeholder="" >
            <el-option v-for="item in ProductOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-input v-model="roleImport" placeholder="" @keyup.native="keyUps()"></el-input>
          <el-button  type="primary" @click="getFindCommodity()"></el-button>
          <el-button type="warning" @click="inputDelete"></el-button>
        </div>
      </div>
      <div class="pro-set">
        <div class="pro-set-left">
          <el-button type="primary" icon="el-icon-plus" @click="dialogFormBtn()"></el-button>
          <el-dialog :title="formLabelMsg+''" :visible.sync="dialogFormVisible">
            <el-form :model="formLabelData" :rules="rules" ref="formLabelData">
              <el-form-item label="" prop="serviceName" :label-width="formLabelWidth">
                <el-input v-model="formLabelData.serviceName" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="checkList">
                <el-checkbox-group v-model="formLabelData.checkList">
                  <el-checkbox :label="item.name" v-for="item in list" :key="item.value"></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="" prop="serviceContent" :label-width="formLabelWidth">
                <el-input
                  type="serviceContent"
                  :rows="2"
                  placeholder=""
                  v-model="formLabelData.serviceContent">
                </el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="cancel('formLabelData')"> </el-button>
              <el-button type="primary" @click="addrole('formLabelData')"> </el-button>
            </div>
          </el-dialog>
        </div>
        <div class="pro-set-page">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="proQueryFormModel.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="rolelistData.total">
          </el-pagination>
        </div>
      </div>
      <div class="pro-table">
        <el-table ref="multipleTable" :data="rolelistData" row-key="id"  @selection-change="change">
          <el-table-column prop="id" label="" type="index" :header-align="tableSet.headerAlign" width="50">
          </el-table-column>
          <el-table-column  type="selection" :header-align="tableSet.headerAlign"  width="50">
          </el-table-column>
          <el-table-column label="" :header-align="tableSet.headerAlign" width="200">
            <!---->
            <template slot-scope="scope">
              <!-- editList(scope.$index, scope.row) -->
              <span style="color:#409EFF;cursor:pointer;" @click="editList(scope.$index, scope.row)"></span>
            </template>
          </el-table-column>
          <el-table-column prop="serviceCode" label="" :header-align="tableSet.headerAlign">
          </el-table-column>
          <el-table-column prop="serviceName" label="" :header-align="tableSet.headerAlign">
          </el-table-column>
          <el-table-column prop="tbcommandNames" label="" :header-align="tableSet.headerAlign">
          </el-table-column>
          <el-table-column prop="serviceContent" label="" :header-align="tableSet.headerAlign">
          </el-table-column>
        </el-table>
      </div>
    </div>

  </div>
</template>

<script>
// import HeaderNav from '../shared/header'
// import apiUrl from '../../utils/api'
// import { roleKeyup } from '@/assets/verification'
import {cityData,eck} from '@/s'
export default {
  name: 'customerCare',
  props: {
    addTab: Function
  },
  data () {
    return {
      list: [],
      item: '',
      name: 'ServeProject',
      textarea: '',
      listArr: [],
      id_arr: [],
      roleImport: '',
      role: {},
      role1: {
        id: '',
        roleName: '',
        description: ''
      },
      rolelistData: [

      ],
      deleteArr: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogFormVisible1: false,
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      residentMenus: [

      ],
      formLabelMsg: '',
      formLabelType: 1,
      formLabelData: {
        serviceName: '',
        commandIds: '',
        checkList: [],
        serviceContent: ''
      },
      rules: {
        serviceName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        commandIds: [
          { required: true, message: '', trigger: 'change' }
        ],
        serviceContent: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      parentList: [],
      formLabelWidth: '120px',
      onDrag: false, // 
      /**  */
      currentPage: 1,
      /**  */
      proParams: { // 
        ProSelect: '1',
        ProSelectInput: ''
      },
      ProductOptions: [ // 
        {
          value: '1', label: ''
        }, {
          value: '2', label: ''
        }
      ],
      ProductOptions1: [ // 

      ],
      isShowAdvancedQuery: false, // 
      brandList: [], // 
      wareHouseList: [], // 
      categoryList: [], // 
      packageTypeList: [], // 
      loading: false, // 
      proQueryFormModel: {
        page: 1, // 
        size: 10 // 
      },
      tableSet: {
        border: true,
        size: 'mini',
        stripe: true,
        tooltipEffect: 'dark',
        headerAlign: 'center'
      },
      CommodityData: {}, // 
      expands: [], // rowkey
      waitOperateCommodityArr: [] // 
    }
  },

  mounted () {
    window.thisVm = this
    this.getroleList()
    this.checkBox()
  },
  methods: {
    cancel (ruleForm) {
      this.$refs[ruleForm].resetFields()
      this.dialogFormVisible = false
    },
    keyUps () {
      this.roleImport = roleKeyup(this.roleImport)
    },
    // 
    dialogFormBtn (type = 1) {
      this.formLabelData.checkList = [];
      this.dialogFormVisible = true
      this.formLabelMsg = type === 1 ? '' : ''
      this.formLabelType = type
      if (type === 1) {
        // 
      }
    },
    change (objVal) {
      this.deleteArr = []
      console.log()
      objVal.forEach((val) => {
        this.deleteArr.push(val.id)
      })
    },
    /**  */
    getroleList () { // 
      // var url = apiUrl.serverProject + '?' + 'pageNumber=' + this.proQueryFormModel.page + '&' + 'pageSize=' + this.proQueryFormModel.size
      // this.httpGet(url, '').then((data) => {
        var data = cityData
        if (data.code === 0) {
          console.log(data)
          this.rolelistData = data.data.list
          this.rolelistData.total = data.data.total
        } else {
        }
      // }).catch((err) => {
      //   this.errorCode(err)
      // })
    },

    editList: function (index, row) {
      this.formLabelData.serviceContent = row.serviceContent
      this.formLabelData.serviceName = row.serviceName
      this.formLabelData.commandIds = row.commandIds
      this.formLabelData.id = row.id
      console.log(row,1)
      console.log(this.formLabelData.checkList,1)
      this.formLabelData.checkList = [];
      this.formLabelData.checkList.push(row.tbcommandNames)
      this.dialogFormBtn(2)
    },
    /**  */
    getFindCommodity (params) { // 
      if (this.roleImport === '') {
        this.roleImport = ' '
      }
      var url = ''
      switch (this.proParams.ProSelect) {
        case '1':
          url += 'serviceCode=' + this.roleImport + '&' + 'serviceName=' + '&' + 'pageNumber=' + this.proQueryFormModel.page + '&' + 'pageSize=' + this.proQueryFormModel.size
          break
        case '2':
          url += 'serviceCode=' + '&' + 'serviceName=' + this.roleImport + '&' + 'pageNumber=' + this.proQueryFormModel.page + '&' + 'pageSize=' + this.proQueryFormModel.size
          break
      }
      if (this.roleImport === '') {
        this.$message.success('')
      }
      this.httpGet(apiUrl.serverProject + '?' + url).then((data) => {
        if (data.code === 0) {
          this.rolelistData = data.data.list
          this.rolelistData.total = data.data.total
        } else {
          console.log('')
        }
      })
    },
    /**  */
    inputDelete: function () { // input
      this.roleImport = ''
      this.getroleList()
    },
    /**  */
    checkBox () {
      var data = eck;
      // this.httpGet(apiUrl.checkBox).then((data) => {
        if (data.code === 0) {
          window.thisVm.list = data.data

          /* window.thisVm.data.data.forEach((v) => {
            this.listArr.push(v.name)
          }) */
      // console.log(data,1122)
        }
      // }).catch((err) => {
      //   console.log(err)
      // })
    },
    /**  */
    addrole (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var ids = []
          this.formLabelData.checkList.forEach(v => {
            var _index = Number(this.listArr.indexOf(v))
            if (_index >= 0) {
              var id = this.list[_index].id
              ids.push(id)
            }
          })
          var _id = ids.join(',')
          this.formLabelData.commandIds = _id
          if (this.formLabelType === 1) {
            delete this.formLabelData.checkList
            this.httpPost(apiUrl.serverProject, this.formLabelData).then((data) => {
              this.callback(data)
              // this.formLabelData = ''
            }).catch((err) => {
              console.log(err)
            })
          } else {
            delete this.formLabelData.checkList
            this.httpPut(apiUrl.serverProject, this.formLabelData).then((data) => {
              this.callback(data)
            }).catch((err) => {
              console.log(err)
            })
          }
        } else {
          return false
        }
      })
    },
    callback (data) {
      if (data.code === 0) {
        // 
        this.getroleList()
        this.dialogFormVisible = false
        window.thisVm.dialogFormVisible = false
      } else {
        this.$message.error(data.msg)
      }
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    handleNodeClick: function (data) { // 
      // 
      this.selectNodeData = data // , 
      this.proQueryFormModel.code = data.code // 
      this.getFindCommodity(this.proQueryFormModel)
    },

    getFilterCategory: function () { // 
      this.httpGet(apiUrl.category + 'searchList').then((res) => {
        if (res.code === 0) {
          this.categoryList = res.data
        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },

    remoteCategoryMethod: function (query) { // 
      if (query !== '') {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.categoryOptions = this.categoryList.filter(item => {
            return item.name.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 200)
      } else {
        this.categoryOptions = []
      }
    },
    /**  */
    // 
    handleSizeChange (val) {
      // this.proQueryFormModel.page = val
      // this.getroleList(this.proQueryFormModel)
      this.proQueryFormModel.size = val
      this.getroleList()
    },
    // 
    handleCurrentChange (val) {
      // this.proQueryFormModel.page = val
      // this.getroleList(this.proQueryFormModel)
      this.proQueryFormModel.page = val
      this.getroleList()
    },

    handleDelete: function (index, row) { // 
      let params = {
        ids: ''
      }
      params.ids = row.id + ''
      this.delComFunction(params)
    },

    deleteCommidityArr () { // 
      if (this.deleteArr.length === 0) {
        return
      };
      let params = {
        ids: this.deleteArr
      }
      this.delComFunction(params)
    },
    delComFunction: function (params) { // 
      this.$confirm(',', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.httpDelete(apiUrl.delleterole + params.ids).then((res) => {
          if (res.code === 0) {
            window.thisVm.getroleList()
          }
        }).catch((err) => {
          console.log(err)
        })
      }).catch(() => {

      })
    }
  },
  watch: {
    filterText (val) {
      this.$refs.tree2.filter(val)
    }
  }
}
</script>

<style>
  #wrap_a .el-header {padding: 0}
  #wrap_a .tab-container {height:30px;width: 100%}
  #wrap_a .tab {width: 100%;height: 30px;}
  #wrap_a .slide {float: left;height: calc(100vh - 106px);overflow: hidden}
  #wrap_a .tree-set {margin-bottom: 10px;}
  #wrap_a .tree-filter-input {width: 180px;margin: 0 auto;}
  #wrap_a .filter-tree {margin-top: 10px;}
  #wrap_a .middle-line {width: 4px;height: calc(100vh - 106px);float: left; background-color: #efefef;position: relative;cursor: ew-resize;}
  #wrap_a .arrow {width: 4px;height: 100px;position: absolute;left: 0;top: 50%;margin-top: -50px;background-color: red;}
  #wrap_a .pro-filter, .pro-set {text-align: left;margin-bottom: 10px;overflow: auto}
  #wrap_a .pro-filter .el-input {width: 180px;}
  #wrap_a .advanced-query .el-form-item.el-form-item--mini {margin-bottom: 10px;}
  #wrap_a .pro-set-left {float: left;margin-left: 30px;}
  #wrap_a .pro-set-page {float: right;}
  #wrap_a .pro-set-page .el-pagination button,.pro-set-page .el-pager li  {min-width: 24px;}
  #wrap_a .el-table thead th {background-color: #ddd}
</style>



